// validation.ts

export namespace Validation {
    export class BookValidator {
        static validateTitle(title: string): string | null {
            if (!title.trim()) {
                return "  ";
            }
            return null;
        }

        static validateAuthor(author: string): string | null {
            if (!author.trim()) {
                return " ' ";
            }
            return null;
        }

        static validateYear(year: string): string | null {
            if (!year.trim()) {
                return "  .";
            }
            const yearNumber = parseInt(year);
            if (isNaN(yearNumber)) {
                return " .";
            }
            const currentYear = new Date().getFullYear();
            const yearOfTheFirstBook = -4000
            if (yearNumber < yearOfTheFirstBook || yearNumber > currentYear) {
                return `     ${yearOfTheFirstBook}  ${currentYear}`;
            }
            return null;
        }
    }

    export class UserValidator {
        static validateName(name: string): string | null {
            if (!name.trim()) {
                return " ' ";
            }
            return null;
        }

        static validateEmail(email: string): string | null {
            if (!email.trim()) {
                return " mail  ";
            }
            const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
            if (!emailRegex.test(email)) {
                return "Email  ";
            }
            return null;
        }

        static validateUserId(id: string): string | null {
            if (!id.trim()) {
                return " ID ";
            }
            if (!/^\d+$/.test(id)) {
                return "ID     ";
            }
            return null;
        }
    }
}